/***** CSS Magic to Highlight Stars on Hover *****/
.rating-my > input:checked ~ label,
.rating-my:not(:checked) > label:hover,
.rating-my:not(:checked) > label:hover ~ label {
  color: #FFD700; }

/* hover previous stars in list */
.rating-my > input:checked + label:hover,
.rating-my > input:checked ~ label:hover,
.rating-my > label:hover ~ input:checked ~ label,
